@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

/* Custom styles for stock market app */
.App {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* Custom input styles */
input[type="text"], 
input[type="number"], 
input[type="date"], 
textarea, 
select {
    @apply border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500;
}

/* Custom button styles */
.btn-primary {
    @apply bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500;
}

.btn-secondary {
    @apply bg-gray-300 text-gray-700 px-4 py-2 rounded-md hover:bg-gray-400 focus:outline-none focus:ring-2 focus:ring-gray-500;
}

.btn-danger {
    @apply bg-red-600 text-white px-4 py-2 rounded-md hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-red-500;
}

/* Custom card styles */
.card {
    @apply bg-white rounded-lg shadow-md p-6;
}

.card-header {
    @apply bg-gray-50 px-6 py-4 border-b rounded-t-lg;
}

/* Sidebar animation */
.sidebar-item {
    @apply transition-colors duration-200 ease-in-out;
}

/* Custom scrollbar styles for portfolio table */
.portfolio-scroll {
    scrollbar-width: thin;
    scrollbar-color: #6b7280 #f3f4f6;
}

.portfolio-scroll::-webkit-scrollbar {
    width: 8px;
}

.portfolio-scroll::-webkit-scrollbar-track {
    background: #f3f4f6;
    border-radius: 4px;
}

.portfolio-scroll::-webkit-scrollbar-thumb {
    background: #6b7280;
    border-radius: 4px;
}

.portfolio-scroll::-webkit-scrollbar-thumb:hover {
    background: #4b5563;
}

/* Dark mode scrollbar */
.dark .portfolio-scroll {
    scrollbar-color: #9ca3af #374151;
}

.dark .portfolio-scroll::-webkit-scrollbar-track {
    background: #374151;
}

.dark .portfolio-scroll::-webkit-scrollbar-thumb {
    background: #9ca3af;
}

.dark .portfolio-scroll::-webkit-scrollbar-thumb:hover {
    background: #d1d5db;
}

/* Custom gray shades for TradeStation theme */
.from-gray-850 {
    --tw-gradient-from: #1a1a1a;
    --tw-gradient-to: rgb(26 26 26 / 0);
    --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
}

.to-gray-850 {
    --tw-gradient-to: #1a1a1a;
}

.bg-gray-850 {
    background-color: #1a1a1a;
}

.hover\:from-gray-750:hover {
    --tw-gradient-from: #262626;
    --tw-gradient-to: rgb(38 38 38 / 0);
    --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
}

.hover\:to-gray-850:hover {
    --tw-gradient-to: #1a1a1a;
}

/* TradeStation style metallic double arrow with gradient and glow */
.ts-double-arrow {
    position: relative;
    width: 16px;
    height: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
}

.ts-double-arrow::before,
.ts-double-arrow::after {
    content: '';
    position: absolute;
    width: 8px;
    height: 8px;
    background: linear-gradient(135deg, 
        #f8fafc 0%, 
        #e2e8f0 15%, 
        #cbd5e1 30%, 
        #94a3b8 45%, 
        #64748b 60%, 
        #475569 75%, 
        #334155 90%, 
        #1e293b 100%
    );
    clip-path: polygon(50% 100%, 0% 0%, 100% 0%);
    transition: all 0.3s ease;
    filter: drop-shadow(0 1px 3px rgba(0,0,0,0.3)) drop-shadow(0 0 6px rgba(255,255,255,0.2));
}

/* Double arrows pointing down (collapsed state) */
.ts-double-arrow::before {
    top: 2px;
}

.ts-double-arrow::after {
    top: 7px;
}

/* Double arrows pointing up (expanded state) */
.ts-double-arrow.expanded::before,
.ts-double-arrow.expanded::after {
    clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

.ts-double-arrow.expanded::before {
    top: 7px;
}

.ts-double-arrow.expanded::after {
    top: 2px;
}

/* Hover effects with enhanced glow */
.ts-double-arrow:hover::before,
.ts-double-arrow:hover::after {
    filter: drop-shadow(0 0 8px rgba(59, 130, 246, 0.8)) 
            drop-shadow(0 1px 3px rgba(0,0,0,0.4)) 
            drop-shadow(0 0 12px rgba(147, 197, 253, 0.4));
    transform: scale(1.15);
    background: linear-gradient(135deg, 
        #dbeafe 0%, 
        #bfdbfe 15%, 
        #93c5fd 30%, 
        #60a5fa 45%, 
        #3b82f6 60%, 
        #2563eb 75%, 
        #1d4ed8 90%, 
        #1e40af 100%
    );
}

/* Active state */
.ts-double-arrow:active::before,
.ts-double-arrow:active::after {
    transform: scale(0.95);
    filter: drop-shadow(0 0 4px rgba(59, 130, 246, 0.6)) 
            drop-shadow(0 1px 2px rgba(0,0,0,0.5));
}

/* Chart container */
.chart-container {
    @apply bg-white p-6 rounded-lg shadow-md;
}

/* Table styles */
.table-container {
    @apply bg-white rounded-lg shadow-md overflow-hidden;
}

.table-header {
    @apply bg-gray-50;
}

.table-row {
    @apply hover:bg-gray-50 transition-colors duration-150;
}

/* Loading spinner */
.loading-spinner {
    @apply animate-spin rounded-full border-2 border-blue-500 border-t-transparent;
}

/* Responsive utilities */
@responsive {
    .sidebar-mobile {
        @apply fixed inset-y-0 left-0 z-50 w-64 transform transition-transform duration-300 ease-in-out;
    }
}

/* Custom scrollbar */
::-webkit-scrollbar {
    width: 6px;
}

::-webkit-scrollbar-track {
    background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
    background: #c1c1c1;
    border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
    background: #a8a8a8;
}

/* Custom scrollbar for sidebar */
.custom-scrollbar::-webkit-scrollbar {
    width: 4px;
}

.custom-scrollbar::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.1);
}

.custom-scrollbar::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.3);
    border-radius: 2px;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.5);
}

/* Stock price colors */
.price-positive {
    @apply text-green-600;
}

.price-negative {
    @apply text-red-600;
}

.price-neutral {
    @apply text-gray-600;
}

/* Badge styles */
.badge {
    @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
}

.badge-success {
    @apply bg-green-100 text-green-800;
}

.badge-danger {
    @apply bg-red-100 text-red-800;
}

.badge-warning {
    @apply bg-yellow-100 text-yellow-800;
}

.badge-info {
    @apply bg-blue-100 text-blue-800;
}

/* Modal backdrop */
.modal-backdrop {
    @apply fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50;
}

.modal-content {
    @apply bg-white rounded-lg shadow-xl max-w-md w-full max-h-screen overflow-y-auto;
}

/* Animation utilities */
@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes slideUp {
    from { transform: translateY(10px); opacity: 0; }
    to { transform: translateY(0); opacity: 1; }
}

.animate-fadeIn {
    animation: fadeIn 0.3s ease-out;
}

.animate-slideUp {
    animation: slideUp 0.3s ease-out;
}

/* Market status indicators */
.market-open {
    @apply bg-green-100 text-green-800 px-2 py-1 rounded text-xs font-medium;
}

.market-closed {
    @apply bg-red-100 text-red-800 px-2 py-1 rounded text-xs font-medium;
}

/* Responsive grid utilities */
.grid-responsive {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

@media (min-width: 768px) {
    .grid-responsive {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 1024px) {
    .grid-responsive {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (min-width: 1280px) {
    .grid-responsive {
        grid-template-columns: repeat(4, 1fr);
    }
}

/* Print styles */
@media print {
    .sidebar {
        display: none;
    }
    
    .main-content {
        margin-left: 0;
    }
}
